<template>
    <div style="padding: 20px;">
        <div class="inner">
            <label>Add Image</label>
            <form @submit.prevent="onSubmit">
                <input class="form-control" v-model="linkImage" type="text" required/>
                <div>
                    <select class="form-control" v-model="selectedType" @change="onChange($event)" required>
                        <option disabled value="">Please choose type of image</option>
                        <option v-for="(type, index) in types" :key="index" :value='type.value'>{{ type.name }}</option>
                    </select>
                    <button type="submit" class="btn btn-success">
                        <i v-if="creatingImage" class="fas fa-spinner fa-spin"></i>
                        <span v-else>Submit</span>
                    </button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    name: 'image_create',
    data() {
        return {
            types: [
                {name: 'Girl', value: 'girl'},
                {name: 'Boy', value: 'boy'},
                {name: 'Game', value: 'game'},
                {name: 'Sexy', value: 'sexy'},
            ],
            linkImage: '',
            selectedType: '',
        }
    },
    methods: {
        onChange: function({target}) {
            this.selectedType = target.value
        },
        onSubmit: function() {
            const image = {
                field: this.images.length+1,
                url: this.linkImage,
                id: parseInt(this.images.length+1, 10),
                type: this.selectedType
            }
            this.$store.dispatch('newImageAction', image)
            this.onClear()
        },
        onClear: function() {
            this.linkImage = ''
        }
    },
    computed: mapGetters(["creatingImage", "images"])
}
</script>

<style scoped>
    .inner {
        padding: 20px;
        background: #000000ad;
    }
    label {
        text-align: center;
        font-size: 24px;
        font-weight: bolder;
        color: white;
    }
    form div {
        margin-top: 10px;
    }
    input {
        border-radius: 4px;
        -webkit-box-shadow: none;
        box-shadow: none;
        outline: 0;
        border: 0;
        padding: 5px 15px;
        width: 30%;
        vertical-align: middle;
        display: inline-block;
    }
    select {
        display: inline-block;
        border-radius: 4px;
        -webkit-box-shadow: none;
        box-shadow: none;
        outline: 0;
        border: 0;
        padding: 5px 15px;
        width: 23.5%;
        vertical-align: middle;
    }
    button {
        margin-left: 5px;
        border-radius: 10px;
    }
</style>
