<template>
  <div class="image_filter">
    <ul>
      <li 
        v-for="(type, index) in types" 
        :key="index" 
        :class="{'activeFilter': type.value == filter}"
        @click="filterAnime(type.value)"
      >
        {{ type.name }}
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: 'image_filter',
  data() {
    return {
      types: [
        {name: 'Anime Girl', value: 'girl'},
        {name: 'Anime Boy', value: 'boy'},
        {name: 'Anime Game', value: 'game'},
        {name: 'Anime Sexy', value: 'sexy'},
      ],
    }
  },
  computed: mapGetters(["filter"]),
  methods: {
    filterAnime: function(name) {
      this.$store.dispatch("filterImageAction", name)
    }
  },
}
</script>

<style scoped>
  div {
    background: #e67e7e;
  }
  ul {
    margin: 0;
    margin-top: 10px;
  }
  ul li {
    list-style-type: none;
    display: inline-block;
    padding: 10px 20px;
    color: white;
    font-weight: bolder;
    font-size: 18px;
    cursor: pointer;
  }
  ul li:hover {
    color: purple;
  }
  .activeFilter {
    color: purple
  }
</style>
