<template>
  <div class="image_list">
    <h1>List of images</h1>
    <div>
      <div v-if="loadingImage" class="lds-default"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
      <transition-group name="fade" tag="div" v-else-if="images.length != 0">
        <div 
          class="d-inline-block m-4 image" 
          v-for="(item, index) in images" :key="index++"
        >
          <div @click="deleteImage(item.id)" class="inner-div"><span>X</span></div>
          <img :src="item.url"/>
        </div>
      </transition-group>
      <div v-else>
        <p class="empty">No image</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'image_list',
  data() {
    return {
      loading: '',
    }
  },
  computed: mapGetters(["loadingImage", "images"]),
  methods: {
    deleteImage: function(id) {
      this.$store.dispatch("deleteImageAction", id)
    }
  },
  created() {
    this.$store.dispatch('fetchImageAction')
  }
}
</script>

<style scoped>
  .empty {
    text-align: center;
    padding: 10px;
    font-size: 24px;
    color: red;
  }
  .image {
    position: relative;
  }
  .image:hover .inner-div {
    visibility: visible;
    opacity: 0.5;
  }
  .image .inner-div {
    background: red;
    height: 100%;
    width: 100%;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
    transition: visibility 0s, opacity 0.5s linear;
  }
  .image span {
    position: absolute;
    top: 41%;
    left: 47%;
    font-size: 26px;
    color: white;
  }
  img {
    width: 200px;
    height: 220px;
  }

  .fade-enter-active {
    /* transition: opacity .7s; */
    animation: scale-up-hor-center .5s ease-out both;
    transform-origin: 50% 50%;
  }

  .fade-leave-active {
    animation: removed-item-animation .4s ease-out forwards;
    transform-origin: 50% 50%;
  }

  .fade-enter {
    opacity: 0;
  }

  @keyframes scale-up-hor-center {
    0% {
      opacity: 0;
      transform: scaleX(0);
    }
    100% {
      opacity: 1;
      transform: scaleX(1);
    }
  }

  /* .fade-leave-to */
  @keyframes removed-item-animation {
    0% {
        opacity: 1;
        transform: scale(1);
    }
 
    100% {
        opacity: 0;
        transform: scale(4);
    }
  }
</style>
